<template>
    <div class="my-ships">
        <navigate title="我的船舶" position="absolute"/>
        <div class="scroll-wrapper" ref="scrollWrapper">
            <mu-load-more v-if="list.length" :refreshing="refresh" :loading="loading"
                          @refresh="onRefresh"
                          @load="onLoad">
                <swipe-cell class="border-1px-b" :right-width="88" v-for="(item, index) in list" :key="index">
                    <div class="list-item"
                         @click="handlerMessageItem(item, index)">
                        <div class="info">
                            <div class="ship-name">{{item.boat.name}}</div>
                            <div>MMSI：{{item.boat.mmsi}}</div>
                        </div>
                        <div class="ship-state">
                            <!--{{item.boat.startState ? "在航" : "停泊"}}-->
                        </div>
                    </div>
                    <mu-button class="delete" slot="right" color="#fff" @click="handlerDelete(item, index)" flat>
                        删除
                    </mu-button>
                </swipe-cell>
            </mu-load-more>
            <no-success text="您还没有添加任何船舶" v-else/>
        </div>
        <transition name="views-left">
            <keep-alive>
                <router-view class="views"/>
            </keep-alive>
        </transition>
    </div>
</template>

<script>
    import Navigate from '../../../base/navigate/navigate'
    import NoSuccess from "../../../components/no-success/no-success"
    import SwipeCell from 'vant/lib/swipe-cell'
    import 'vant/lib/swipe-cell/style'
    import {queryIBoatList, deleteIBoatById} from "../../../api/personal"

    export default {
        name: "myShips",
        data() {
            return {
                /***
                 * 刷新和加载更多
                 */
                refresh: false,
                loading: false,
                /***
                 * 我的船舶列表
                 */
                list: [],
                /***
                 * 检索信息
                 */
                params: {
                    pageNumber: 0,
                    pageSize: 10
                }
            }
        },
        activated() {
            this.queryIBoatList()
        },
        methods: {
            /***
             * 查看 我的船舶 详情
             */
            handlerMessageItem(item) {
                this.$router.push({
                    name: 'about-ship-detail',
                    query: {
                        typeId: '013001',
                        hideCollection:true
                    },
                    params: {
                        type: '013001',
                        id: item.boat.mmsi
                    }
                })
            },
            /***
             * 获取 我的船舶 列表
             */
            queryIBoatList() {
                this.params.pageNumber = 0

                return queryIBoatList(this.params).then(res => {
                    this.list = res
                    this.refresh = false
                })
            },
            /***
             * 下拉刷新
             */
            onRefresh() {
                this.refresh = true
                this.$refs.scrollWrapper.scrollTop = 0

                this.queryIBoatList()
            },
            /***
             * 上拉加载
             */
            onLoad() {
                this.loading = true
                this.params.pageNumber++

                queryIBoatList(this.params).then(res => {
                    this.list.push(...res)
                    this.loading = false
                })
            },
            /***
             * 删除
             * @param item  item.id => 收藏 ID
             * @param index 索引
             */
            handlerDelete(item, index) {
                deleteIBoatById(item.id).then(res => {
                    this.$toast('删除成功')
                    this.list.splice(index, 1)
                })
            }
        },
        components: {
            NoSuccess,
            Navigate,
            SwipeCell
        }
    }
</script>

<style lang="stylus">
    @import "../../../common/stylus/variable.styl"

    .my-ships
        padding-top 46px
        background-color #fff
        .list-item
            position relative
            display flex
            align-items center
            padding 15px
            .info
                flex 1
                color #C4C4C4
                font-size $font-size-ms
                .ship-name
                    margin-bottom 10px
                    font-size $font-size-ls
                    color #3D3D3D
            .ship-state
                align-self flex-start
                margin-top 7px
                font-size $font-size-ms
        .delete
            width 100%
            height 100%
            background-color #f44336

    #ship-views
        position absolute
        top 0
        left 0
        right 0
        bottom 0
        z-index 100
        background-color #fff
</style>



